/* Customize your main colors in :root variables */
:root {
  --main-background-color: hsl(227, 21%, 26%);
  --card-background-color: #14141438;
  --countdown-background-color: #1b303f;
  --main-text-color:#FFF;
  --title-text-color:#4bd2e5;
}

body{
/*  background-color:var(--main-background-color);*/
  background: url("img/background.png");
  background-size: cover;
  font-family: 'Merriweather', cursive;
  font-size: 1.1em;
}

h2, p{
  color: var(--main-text-color);
  font-weight: 10000;
}

h3{
font-weight: normal;
}



